.button{
    position: absolute;
    right : 1rem;
    padding : 0.5rem;
    background-color:#F7DC6F ;
    color : crimson;
    border-radius: 10px;
    font-size: 1.2rem;
    border : 2px solid white;
    cursor : pointer;
}

.signup{
    background-color: lightsalmon;
    color : rgb(32, 30, 30);
}

.button::after{
    position: absolute;
    content: "";
    border-radius: 10px;
    padding : 0.3rem 0.4rem 0.3rem  0.4rem;
    background-image: linear-gradient(rgba(255, 255, 255, 0.993),rgba(255, 255, 255, 0.993));
    right : 5%;
    top:3%;
}

.button:hover{
    transform: scale(0.98);
}