@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Roboto+Mono:wght@500&family=Yanone+Kaffeesatz&display=swap');

.sliderBox{
    position: relative;
    box-sizing: border-box;
    display: flex;
    width: 100%;
    height: 50vh;
    min-height: 400px;
    /* background-color: rgba(15, 15, 15, 0.938); */
    background-image: url(../../Images/SliderImage.png);
    background-position: center center;
    background-size:cover;
    overflow: hidden;
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 2rem;
    letter-spacing: 2px;
    color:gold;
}

.sliders{
    position: absolute;
    border-radius: 5px;
    padding : 0.5rem 1rem 0.5rem 1rem;
    background-color: transparent;
    cursor:default;
    font-size: 4rem;
    color:rgb(250, 247, 247);
    cursor: pointer;
}


.left{
    left:0;
    top:43%;
}

.right{
    right:0;
    top:43%;
}

.sliderImage{
    position: absolute;
    display: flex;
    min-width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    /* background-color:rgba(39, 36, 36, 1); */
    background-color: transparent;
    transition:all 700ms linear;
    scroll-behavior: smooth;
}

.sliderInnerBox{
    display: flex;
    flex-direction: column;
    width: 80%;
    place-items: center;
    color:white;
}

.sliderInnerBox>p{
    font-size: 1.9rem;
    letter-spacing: 1px;
    word-spacing: 2.5px;
    overflow-y: scroll;
}

.sliderInnerBox>h1{
    font-size: 3.5rem;
    letter-spacing: 2.5px;
    color: gold;
}

.fastTransition{
    transition: all 1ms linear;
}

.moreButton{
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
    color : rgb(12, 11, 11);
    font-size:1.2rem;
    transform: translateY(-5px);
}

.moreButton:hover{
    background-color: rgba(255, 255, 255, 0.884);
    color: rgba(12, 11, 11, 0.904);
}

.sliderFont{
    font-family: 'Yanone Kaffeesatz', sans-serif;   
}