::-webkit-scrollbar{
    display: none;
}

.body{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    min-height: 400px;
    background-image: url(../../Images/Heallthify.png);
    background-size:cover;
    background-repeat:round;
}

.bodyRegister{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    min-height: 700px;
    background-image: url(../../Images/Heallthify.png);
    background-size:cover;
    background-repeat:round;
}

.mainBox{
    display: grid;
    flex-direction: column;
    grid-template-columns: 0.5fr 1fr;
    justify-content: center;
    padding-top:1rem;
    align-items:center;
    height: 100%;
    /* min-height: 600px; */
    /* background-image:linear-gradient(rgba(66, 241, 43, 0.952),rgba(66, 241, 43, 0.877),rgba(66, 241, 43, 0.774),rgba(66, 241, 43, 0.781),rgba(66, 241, 43, 0.664)); */
    /* background-color: rgba(135, 207, 235, 0.911); */
}

.header{
    margin-top:0;
    background-color: transparent;
    color: transparent;
    padding : 1rem;
    font-size: 2rem;
    margin-bottom: 0;
    cursor:default;
    height: 6%;
}

.loginBox{
    display: flex;
    position: relative;
    flex-direction: column;
    padding : 1.7rem;
    border-radius: 10px;
    border:5px solid black;
    justify-self: center;
    align-items: center;
    text-align: center;    
    width: 25%;
    row-gap: 0.6rem;
    background-color:rgba(108, 220, 235, 0.185);
    min-width: 400px;
    min-height: 200px;
    margin-top: -1.5rem;
    box-shadow: 10px 15px 20px rgba(0, 0, 0, 0.479);
}

.loginBox input{
    position: relative;
    padding : 0.5rem;
    font-size: 1.3rem;
    border:2.5px solid transparent;
    border-radius: 5px;
    outline:4px solid transparent;
    transition: all 100ms linear;
    border-color: transparent;
    background-color:white;
    text-align: center;
    width: 100%;
}

.errormssg{
    position: relative;
    color : red;
}

.loginBox input:hover{
    border-color:rgba(207, 4, 247, 1);
}

.loginBox input:focus-within{
    width: 100%;
    border-color:rgba(207, 4, 247, 1);
    outline-color:rgba(207, 4, 247, 0.548);
}

.loginBox>button{
    display: flex;
    justify-content: center;
    left:23%;
    text-decoration: none;
    font-weight: 500;
    font-size: 1.2rem;
    width: 50%;
    padding : 5px;
    background-color: rgb(10, 228, 144);
    /* background-image:linear-gradient(whitesmoke 55%,red 50%);
    -webkit-background-clip: text;
    background-clip:text;
    color : transparent; */
    cursor: pointer;
    border-radius: 2px;
}

.registerMssg{
    padding: 1.2rem;
    background-color: white;
    color: rgb(235, 26, 26);
    font-weight: 500;
    font-size: 1.2rem;
}

.green{
    color :rgba(3, 182, 57, 0.993);
}

.mr_bottom_80px{
    margin-bottom: 80px;
}

.image{
    position: relative;
    width: 5rem;
    height: 4rem;
    border-radius: 10px;
}
